@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --white: #F8F8F8;
  --yellow: #FBAF00;
  --dark-blue: #50A3C6;
  --blue: #79C0D7;
  --light-grey: #848484;
  --black: #36393b;
  --slate-white: #DDDFDF;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body {
  background: rgba(255, 255, 255, 0.4); 
  font-family: 'Roboto', sans-serif;
}

.bg {
  width: 100vw;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: absolute;
  top: 0;
  z-index: -1;
}

.bg .clouds {
  position: relative;
  top: -20px;
}

.blur-layer {
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  background: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
}

.header {
  background-color: #50A3C6;
  background-color: var(--dark-blue);
  padding-bottom: 0;
  color: #F8F8F8;
  color: var(--white);
  box-shadow: 0 4px 6px 1px #00000040;
}

.logo {position: relative; left: -0.5rem; top: -4px;}

.nav {position: relative;}
.nav-item {
  background: none;
  outline: none;
  border: none;
  color: #F8F8F8;
  color: var(--white);
  margin: 0;
  padding-bottom: 3px;
}
.nav-item:first-child {border-bottom: 3px solid #F8F8F8;border-bottom: 3px solid var(--white);}
.nav-line {
  position: absolute;
  bottom: 0;
  left: 25px;
  height: 3px;
  width: 0;
  background-color: #F8F8F8;
  background-color: var(--white);
  transition:all 0.4s;
}

.location {
  font-size: 1.15rem;
  color: #FBAF00;
  color: var(--yellow);
  letter-spacing: 0.08em;
}

.date, .time {
  font-size: 0.7rem;
  color: #848484;
  color: var(--light-grey);
  letter-spacing: 0.08em;
}
.time {
  font-size: 0.8rem;
}

.day, .night {
  font-size: 0.82rem;
  color: #848484;
  color: var(--light-grey);
  line-height: 8px;
  padding: 1px;
  letter-spacing: 1px;
}

.day {padding-top: 7px;}

.current-temp {
  color: #50A3C6;
  color: var(--dark-blue);
  font-size: 4rem;
  text-shadow: 0 3px 6px #0000002c;
}

.main .clouds {
  position: relative;
  top: -15px;
  left: 40px;
  width: 25vw;
}

.main .feels {
  font-size: 1rem;
  color: #36393b;
  color: var(--black);
}

.main .rain {
  font-size: 1.3rem;
  color: #36393b;
  color: var(--black);
  margin: 1.5rem 0;
}

.main .rain > span {font-size: 0.8rem;}

.lower-Body {
  background-color: #36393b;
  background-color: var(--black);
  /* position: absolute;
  bottom: 0; */
  margin-bottom: 0;
}

.hourly-container {
  padding: 16px 0;
  overflow: scroll;
}

.degree {
  width: 50px;
  height: 50px;
  background-color: #50A3C6;
  background-color: var(--dark-blue);
  color: #F8F8F8;
  color: var(--white);
  border-radius: 50%;
  font-size: 0.8rem;
}

.hour-time {
  font-size: 0.8rem;
  margin: 0;
  margin-top: 4px;
  color: #F8F8F8;
  color: var(--white);
}

/* Location access button  */

.first-page {
  height: 70vh;
}

.give-location {
  text-align: center;
  color: #36393b;
  color: var(--black);
}

.btn-container-off {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #DDDFDF;
  background: var(--slate-white);
  width: 130px;
  height: 50px;
  border: 3px solid #36393b;
  border: 3px solid var(--black);
  border-radius: 28px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.small-btn {
  width: 80px;
  height: 36px;
  border-radius: 20px;
  background-color: #36393b;
  background-color: var(--black);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 4px;
  left: 4px;
  cursor: pointer;
}

.light {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: #848484;
  background: var(--light-grey);
  position: absolute;
  right: 10px;
  top: 15px;
}

.btn-container, .small-btn ,.light {
  transition: all 200ms linear;
}

.switch-off {
  color: #F8F8F8;
  color: var(--white);
  font-size: .7rem;
  letter-spacing: 2px;
  margin: 0;
}

.btn-title {
  font-size: 0.8rem;
  color: #50A3C6;
  color: var(--dark-blue);
  margin: 4px;
}

.btn-container-on {
  background-color: #79C0D7;
  background-color: var(--blue);
}

.btn-container-on .small-btn {
  left: 40px;
  background-color: #36393b;
  background-color: var(--black);
}

.btn-container-on .light {
  background-color: #d1f4ff;
}

/* Proloader  */
.sun {
  -webkit-animation: spin 1.8s linear 0s infinite normal forwards;
          animation: spin 1.8s linear 0s infinite normal forwards;
}

@-webkit-keyframes spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spin {
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

.loading {
  font-size: 0.7rem;
  color: #848484;
  z-index: 3;
  position: absolute;
  top: 25px;
  -webkit-animation: upDown 1.8s linear 0s infinite normal forwards;
          animation: upDown 1.8s linear 0s infinite normal forwards;
}

@-webkit-keyframes upDown {
  0% {top: 25px;}
  50% {top: 18px;}
  100% {top: 25px;}
}

@keyframes upDown {
  0% {top: 25px;}
  50% {top: 18px;}
  100% {top: 25px;}
}

/* week page  */
.week {
  border-bottom: 1px solid #79c0d754;
}

.week .date {
  color: #36393b;
  color: var(--black);
}

.week .desc {
  color: #50A3C6;
  color: var(--dark-blue);
  margin: 0;
  letter-spacing: 0.075em;
}
.week .temp {
  font-size: 0.8rem;
  color: #36393b;
  color: var(--black);
  letter-spacing: 0.08em;
  line-height: 30px;
  margin: 0;
}
